/* === Общие стили === */
body {
  font-family: 'Segoe UI', Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: #f7f9fb;
  color: #222;
}

a {
  color: #1976d2;
  text-decoration: none;
  transition: color 0.2s;
}

a:hover {
  text-decoration: underline;
  color: #1565c0;
}

button {
  cursor: pointer;
  background: #1976d2;
  color: #fff;
  border: none;
  padding: 0.6em 1.2em;
  border-radius: 5px;
  font-size: 1em;
  transition: background 0.2s;
}

button:disabled {
  background: #b0bec5;
  cursor: not-allowed;
}

button:hover:not(:disabled) {
  background: #1565c0;
}

input, select {
  padding: 0.5em;
  border: 1px solid #b0bec5;
  border-radius: 4px;
  font-size: 1em;
  margin-top: 0.3em;
  margin-bottom: 0.7em;
  width: 100%;
  box-sizing: border-box;
}

label {
  display: block;
  margin-bottom: 0.5em;
  font-weight: 500;
}

.error {
  color: #d32f2f;
  font-size: 0.95em;
  margin-top: -0.5em;
  margin-bottom: 0.5em;
}

/* === Header === */
header {
  background: #fff;
  border-bottom: 1px solid #e3e3e3;
  padding: 1em 2em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.logo a {
  font-size: 1.5em;
  font-weight: bold;
  color: #1976d2;
  letter-spacing: 2px;
}

nav {
  display: flex;
  gap: 1.5em;
}

nav a {
  font-size: 1em;
  color: #222;
  padding: 0.2em 0.5em;
  border-radius: 3px;
}

nav a:hover {
  background: #e3f2fd;
}

.user-panel {
  display: flex;
  align-items: center;
  gap: 1em;
}

.user-panel .avatar {
  font-size: 1.3em;
  margin-right: 0.5em;
}

.account-info {
  font-size: 0.95em;
  color: #1976d2;
  margin-right: 1em;
}

/* === Footer === */
footer {
  background: #f5f5f5;
  text-align: center;
  padding: 1.2em 0;
  font-size: 1em;
  color: #888;
  border-top: 1px solid #e3e3e3;
  margin-top: 2em;
}

/* === Loader === */
.loader {
  margin: 1em auto;
  text-align: center;
  color: #1976d2;
  font-size: 1.1em;
}

/* === Home Page === */
.home-hero {
  padding: 2em 1em;
  background: #f5faff;
  text-align: center;
}

.home-hero h1 {
  margin-bottom: 0.5em;
  font-size: 2em;
}

.main-btn {
  margin-top: 1.5em;
  padding: 0.8em 2em;
  font-size: 1.1em;
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s;
}

.main-btn:hover {
  background: #1565c0;
}

.home-whyus, .home-tariffs {
  margin: 2em 0;
  padding: 1em;
}

/* === WhyUsCarousel === */
.whyus-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
  margin: 1em 0;
}

.whyus-card {
  background: #fff;
  border: 1px solid #e3e3e3;
  border-radius: 8px;
  padding: 1.5em 2em;
  min-width: 220px;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.04);
  text-align: center;
}

.whyus-card h4 {
  margin-bottom: 0.5em;
  color: #1976d2;
}

/* === Tariffs === */
.tariffs {
  margin-top: 2em;
}

.tariffs-list {
  display: flex;
  gap: 1.5em;
  flex-wrap: wrap;
  justify-content: center;
}

.tariff-card {
  background: #fff;
  border: 1px solid #b0bec5;
  border-radius: 8px;
  padding: 1.5em 1.2em 1.2em 1.2em;
  min-width: 180px;
  position: relative;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.04);
  text-align: center;
  transition: border 0.2s, box-shadow 0.2s;
}

.tariff-card.current {
  border: 2px solid #1976d2;
  box-shadow: 0 4px 16px rgba(25, 118, 210, 0.08);
}

.tariff-card .badge {
  position: absolute;
  top: 0.7em;
  right: 0.7em;
  background: #1976d2;
  color: #fff;
  padding: 0.2em 0.7em;
  border-radius: 3px;
  font-size: 0.85em;
}

.tariff-card button {
  margin-top: 1em;
  width: 100%;
}

/* === AuthForm === */
.auth-form {
  max-width: 350px;
  margin: 2em auto;
  background: #fff;
  border-radius: 8px;
  padding: 2em 2em 1.5em 2em;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.06);
}

.auth-form input {
  margin-bottom: 1em;
}

.auth-form__extra {
  margin-top: 1.5em;
  text-align: center;
}

.auth-form__extra .tab,
.auth-form__extra .link {
  display: inline-block;
  margin: 0 0.5em;
  color: #1976d2;
  cursor: pointer;
  font-size: 0.98em;
}

.auth-form__extra .tab:hover,
.auth-form__extra .link:hover {
  text-decoration: underline;
}

.auth-form__extra .socials {
  margin-top: 1em;
  display: flex;
  gap: 0.5em;
  justify-content: center;
}

.auth-form__extra .socials button {
  background: #e3e3e3;
  color: #222;
  border: none;
  border-radius: 4px;
  padding: 0.4em 0.8em;
  font-size: 0.95em;
  cursor: not-allowed;
}

/* === SearchForm === */
.search-form {
  max-width: 600px;
  margin: 2em auto;
  background: #fff;
  border-radius: 8px;
  padding: 2em 2em 1.5em 2em;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.06);
}

.search-form label {
  margin-bottom: 0.2em;
}

.search-form input[type="checkbox"] {
  width: auto;
  margin-left: 0.5em;
}

.search-form button[type="submit"] {
  margin-top: 1em;
  width: 100%;
}

/* === SummaryCarousel === */
.summary-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
  margin: 2em 0 1em 0;
}

.summary-carousel__item {
  background: #e3f2fd;
  border-radius: 8px;
  padding: 1.2em 2em;
  min-width: 220px;
  text-align: center;
  font-size: 1.1em;
}

.summary-carousel button {
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 2.2em;
  height: 2.2em;
  font-size: 1.2em;
  transition: background 0.2s;
}

.summary-carousel button:hover {
  background: #1565c0;
}

/* === PublicationsList === */
.publications-list {
  margin: 2em 0;
}

.show-more-btn {
  display: block;
  margin: 1.5em auto 0 auto;
  padding: 0.7em 2em;
  font-size: 1em;
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: background 0.2s;
}

.show-more-btn:hover {
  background: #1565c0;
}

/* === PublicationCard === */
.publication-card {
  border: 1px solid #e3e3e3;
  border-radius: 8px;
  margin: 1.5em 0;
  padding: 1.5em 1.2em;
  background: #fff;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.03);
}

.publication-card__header {
  display: flex;
  justify-content: space-between;
  font-size: 0.98em;
  color: #555;
  margin-bottom: 0.7em;
}

.publication-card__tags {
  margin: 0.5em 0;
}

.publication-card__tags .tag {
  display: inline-block;
  background: #e3f2fd;
  color: #1976d2;
  border-radius: 3px;
  padding: 0.2em 0.7em;
  margin-right: 0.5em;
  font-size: 0.95em;
}

.publication-card__content {
  margin: 1em 0;
  color: #222;
  line-height: 1.5;
}

.publication-card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1em;
}

.read-btn {
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 0.4em 1em;
  text-decoration: none;
  transition: background 0.2s;
}

.read-btn:hover {
  background: #1565c0;
}

/* === Адаптивность === */
@media (max-width: 900px) {
  .tariffs-list {
    flex-direction: column;
    align-items: center;
  }
  .summary-carousel, .whyus-carousel {
    flex-direction: column;
    gap: 0.5em;
  }
}

@media (max-width: 600px) {
  header, .search-form, .auth-form, .home-hero, .home-whyus, .home-tariffs {
    padding: 1em;
  }
  .publication-card, .tariff-card, .whyus-card, .summary-carousel__item {
    padding: 1em;
    min-width: unset;
  }
  .summary-carousel__item, .whyus-card {
    font-size: 1em;
  }
}